.levelDiv {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 95%;
  margin-left: 2.5%;
  font-size: 20px;
  border: 1px solid lightgray;
  padding: 2px 2px 2px 10px;
  box-sizing: border-box;
}

.levelItemCustom {
  width: auto;
}

.level {
  display: flex;
  border-radius: 5px;
}

.is-scrollable {
  overflow: auto;
  position: absolute;
  top: 100px;
  bottom: 70px;
  width: 100%;
}

.is-positive {
  background-color: #c7f6c7;
  font-weight: bold;
}

.positiveLabel {
  background-color: #c7f6c7;
  font-weight: bold;
  width: 40px;
  box-sizing: border-box;
}

.is-notSure {
  background-color: palegoldenrod;
  box-sizing: border-box;
  width: 40px;
  font-weight: bold;
}

.is-negative {
  background-color: lightpink;
  box-sizing: border-box;
  font-weight: bold;
  width: 40px;
  /* border: 2px solid red; */
}

.isNegativeStatement {
  background-color: rgba(255, 182, 193, 0.4);
}

.isUncertainStatement {
  background-color: rgba(211, 211, 211, 0.4);
}

.isPositiveStatement {
  background-color: rgba(199, 246, 199, 0.6);
}

.notSet {
  background-color: palegoldenrod;
  font-weight: bold;
}

.listDiv {
  background-color: #f6fbfc;
}

.levelDiv input {
  position: absolute;
  top: 1000;
  right: -1000;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
}

.levelDiv input[type="radio"]:checked + label {
  border: 3px solid black;
}
