.sortContainer {
  display: flex;
  flex-direction: column;
  margin: 5px;
  /* justify-content: flex-start; */
  /* align-items: flex-start; */
  background: rgba(0, 0, 0, 0.1);
  min-height: 400px;
  max-height: 800px;
  overflow: auto;
}

.rootDiv {
  background-color: steelblue;
  box-sizing: border-box;
  padding: 0px, 10px, 10px, 10px;
  min-height: 100vh;
  /* flexbox */
  display: flex;
  /* justify-content: center; */
  justify-content: center;
  align-items: flex-start;
}

.headersContainer {
  display: flex;
  position: fixed;
  justify-content: center;
  align-items: flex-start;
  height: 50px;
  flex-direction: "row-reverse";
}

.greenSortCard {
  background: lightgreen;
  border: 2px solid gray;
}

.pinkSortCard {
  background-color: rgba(255, 182, 193, 0.4);
  border: 2px solid gray;
}

.yellowSortCard {
  background-color: lightgray;
  border: 2px solid gray;
}
/* 
.isPositiveStatement {
  background-color: rgba(199, 246, 199, 0.6);
} */

.sortFooter {
  display: flex;
  flex-direction: row;
  background: #e4e4e4;
  position: fixed;
  left: 0px;
  bottom: 0;
  width: 100%;
  min-height: 150px;
}

.footerMessageBox {
  flex-basis: 300px;
  width: 300px;
  border: 2px solid lightgray;
  text-align: center;
}

.footerMessageBox p {
  padding: 10px;
  margin-top: 20px;
  font-size: 25px;
  color: #3273dc;
}

.sortEndButton {
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 10px;
}

.cardSlider {
  width: 1300px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
