.titleDiv {
  padding-top: 20px;
  height: 100px;
  background: steelblue;
  top: 0;
  left: 0;
  position: absolute;
  overflow: hidden;
  right: 0;
}

.titleDiv h1 {
  color: white;
  font-size: 36px;
}

.scrollableDiv {
  overflow: auto;
  position: absolute;
  top: 100px;
  bottom: 70px;
  left: 0px;
  right: 0px;
  background: #5d80b2;
}

.postSortFooter {
  position: absolute;
  overflow: hidden;
  left: 0;
  bottom: 0;
}