body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.fix-footer {
  position: absolute;
  width: 100%;
  bottom: 0;
  overflow: hidden;
  height: 70px !important;
  padding: 10px !important;
}
.Home {
  text-align: center;
}

.heroDiv {
  background: steelblue;
}

.title {
  color: white;
}

.subtitle {
  color: white;
}

.footerDiv {
  background: steelblue;
}
.NotFound {
  text-align: center;
}

.levelDiv {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 95%;
  margin-left: 2.5%;
  font-size: 20px;
  border: 1px solid lightgray;
  padding: 2px 2px 2px 10px;
  box-sizing: border-box;
}

.levelItemCustom {
  width: auto;
}

.level {
  display: -webkit-flex;
  display: flex;
  border-radius: 5px;
}

.is-scrollable {
  overflow: auto;
  position: absolute;
  top: 100px;
  bottom: 70px;
  width: 100%;
}

.is-positive {
  background-color: #c7f6c7;
  font-weight: bold;
}

.positiveLabel {
  background-color: #c7f6c7;
  font-weight: bold;
  width: 40px;
  box-sizing: border-box;
}

.is-notSure {
  background-color: palegoldenrod;
  box-sizing: border-box;
  width: 40px;
  font-weight: bold;
}

.is-negative {
  background-color: lightpink;
  box-sizing: border-box;
  font-weight: bold;
  width: 40px;
  /* border: 2px solid red; */
}

.isNegativeStatement {
  background-color: rgba(255, 182, 193, 0.4);
}

.isUncertainStatement {
  background-color: rgba(211, 211, 211, 0.4);
}

.isPositiveStatement {
  background-color: rgba(199, 246, 199, 0.6);
}

.notSet {
  background-color: palegoldenrod;
  font-weight: bold;
}

.listDiv {
  background-color: #f6fbfc;
}

.levelDiv input {
  position: absolute;
  top: 1000;
  right: -1000;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
}

.levelDiv input[type="radio"]:checked + label {
  border: 3px solid black;
}

.sortContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 5px;
  /* justify-content: flex-start; */
  /* align-items: flex-start; */
  background: rgba(0, 0, 0, 0.1);
  min-height: 400px;
  max-height: 800px;
  overflow: auto;
}

.rootDiv {
  background-color: steelblue;
  box-sizing: border-box;
  padding: 0px, 10px, 10px, 10px;
  min-height: 100vh;
  /* flexbox */
  display: -webkit-flex;
  display: flex;
  /* justify-content: center; */
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.headersContainer {
  display: -webkit-flex;
  display: flex;
  position: fixed;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  height: 50px;
  -webkit-flex-direction: "row-reverse";
          flex-direction: "row-reverse";
}

.greenSortCard {
  background: lightgreen;
  border: 2px solid gray;
}

.pinkSortCard {
  background-color: rgba(255, 182, 193, 0.4);
  border: 2px solid gray;
}

.yellowSortCard {
  background-color: lightgray;
  border: 2px solid gray;
}
/* 
.isPositiveStatement {
  background-color: rgba(199, 246, 199, 0.6);
} */

.sortFooter {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  background: #e4e4e4;
  position: fixed;
  left: 0px;
  bottom: 0;
  width: 100%;
  min-height: 150px;
}

.footerMessageBox {
  -webkit-flex-basis: 300px;
          flex-basis: 300px;
  width: 300px;
  border: 2px solid lightgray;
  text-align: center;
}

.footerMessageBox p {
  padding: 10px;
  margin-top: 20px;
  font-size: 25px;
  color: #3273dc;
}

.sortEndButton {
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 10px;
}

.cardSlider {
  width: 1300px;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.titleDiv {
  padding-top: 20px;
  height: 100px;
  background: steelblue;
  top: 0;
  left: 0;
  position: absolute;
  overflow: hidden;
  right: 0;
}

.titleDiv h1 {
  color: white;
  font-size: 36px;
}

.scrollableDiv {
  overflow: auto;
  position: absolute;
  top: 100px;
  bottom: 70px;
  left: 0px;
  right: 0px;
  background: #5d80b2;
}

.postSortFooter {
  position: absolute;
  overflow: hidden;
  left: 0;
  bottom: 0;
}
